.Cocktail{
    width: 50%;
    height: 72%;
    padding-top: 10%;
    background-image: linear-gradient(#96c024a1, rgba(165, 42, 42, 0.5));
    
    clip-path: polygon(50% 0, 0 100%, 100% 100%);
    border-radius: 30%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.Cocktail img{
    width: 20%;
    transition: width 0.5s;
}

.Cocktail img:hover{
    width: 22%;
}

.Container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: auto;

}

@media (max-width: 500px) {
    .Cocktail{
        width: 100%;
        align-items: center;
    }
    
    .Cocktail img{
        width: 40%;
        transition: width 0.5s;
    }
    
    .Cocktail img:hover{
        width: 50%;
    }
    
}